<template>
	<div id="a">
			<div class="fu" :class="{'fu-l':kg1}">
			<div  class="fu-ll"></div>
			<div class="fu-hade">
			    {{textal}}
			
			 <button @click="kg1=false" >换一把 </button>
			</div>
			</div>
		<div class="hade">
			<img @click="fan()" src="./img/wd3x.png" />{{aa}}
			<div @click="yz()" class="hade-div" v-if="shouji">
				你尚未完成手机验证, 请验证 <img src="./img/yj3x.png" />
			</div>
			<div  class="hade-diva" v-if="kg2">
				{{textal}}
				 <img :src="kg3?imgv:imgx" :class="{'v':kg3}" />
			</div>
			<div  class="hade-axs" v-if="axs">
				<div class="qq qq1">
					<div>
						<p>{{yzdata.jl}}</p>{{yzdata.m}}
					</div>
					<p><img src="./img/jl3x.png" />距离</p>
				</div>
				<div class="qq">
					<div>
						<p>{{yzdata.kj}}</p>把
					</div>
					<p><img src="./img/kej3x.png" />可借</p>
				</div>
			</div>
		</div>
		<div class="body" id="container">
		</div>
		<div class=" foot">
			<div>
				<img @click="setgo()"class="cli" src="./img/sxdw3x.png" />
				<img @click="kefu()" class="kf im cli" src="./img/kf3x.png" />
				<img @click="diwei()" class="im cli" src="./img/wdwz3x.png" />
				<img class="sys" src="./img/ysy3x.png" />
				<div @click="sys()">扫码开伞</div>
			</div>
		</div>
	</div>
</template>

<script>
	const xwd = require('../../assets/js/common_z.js');
	const jq = require('../../assets/js/jquery-3.2.1.min.js');
   import imgx from './img/X-1.png';
   import imgv from './img/v-2.png';
	import imgls from './img/san3x.png';
	import imgwz from './img/wz3x.png';
	import imgyizi from './img/aixiyi3x.png';
    
	export default {
		data() {
			return {
				imgx,imgv,
				textal:'伞的电量过低,暂时无法使用!',
				imgls: imgls,
				imgwz: imgwz,
				imgyizi: imgyizi,
				shouji: true,
				axs: false,
				weizi:[114.2909500000, 22.7219600000],
				weizia:[114.2939500000, 22.7219600000],
				lsico: [{"umbrellaNum": "34244324",
						"lng": "114.291872",
						"lat": "22.72089",
						"distance": 199.999,
						"typeName": "晴雨伞"
					}
				],
				yzico: [{
						"umbrellaPileNum": "6549645",
						"lng": "114.293872",
						"lat": "22.72189",
						"distance": 199.999,
						"availableNum": 20,
						"umbrellaBorrowList": [{
								"typeName": "晴雨伞",
								"num": 10
							},
							{"typeName": "帅哥伞",
								"num": 10}]
					},{
						"umbrellaPileNum": "6549645",
						"lng": "114.292872",
						"lat": "22.72189",
						"distance": 199.999,
						"availableNum": 22,
						"umbrellaBorrowList": [{
								"typeName": "晴雨伞",
								"num": 12
							},
							{
								"typeName": "帅哥伞",
								"num": 10
							}
						]
					}
				],
				yzdata: {
					jl: 1.01,m: 'm',kj: 100,ls1: 23,ls2: 123,ls3: 223,ls4: 253,ls5: 53
				},
				map: '',
				marker:'',
				aa:1,
				kg:true,
				kg2:false,
				kg3:false,
				kg1:false
			}
		},
		mounted: function() {
			console.log(window.screen.width)
		this.aa=window.screen.width/(23.4375*16);
			var thisA = this;
		 $('.cli').on('touchstart',function(){
		 	//console.log('1');
            $(this).css({'opacity':'0.5' })  //隐藏div
       }).on('touchend',function(){
       //	console.log('2');
            $(this).css({'opacity':'1' })   //显示，固定div
        })
       
        thisA.map = new AMap.Map('container', {
				resizeEnable: true,
				zoom: 17,
				center: [116.397392,39.908708]
			})
       
			xwd.function21007(function(dat){
				
				if(dat){xwd.leid=dat 
					thisA.shouji= false }else{thisA.shouji= true};
              // thisA.aa=dat;
             // console.log()
				thisA.getgo();
			});
				//h5Data[21007](false);
			 !(window.Android&& window.JsCallOC)&&window.javaCallJs();
			
			 window.clearInterval(xwd.timeA);
			 xwd.timeA=setInterval(function () {thisA.getgo()},10000)
		},
		updated:function  () {
			
		},
		methods: {
			diwei: function() {

				this.map.setCenter(this.weizi);
			},
			getgo: function() {
				var thisA = this;
			   
                xwd.function21000(function (dat) {
                	//alert(data);
               	 //data=data.split(","); 
                	thisA.weizi=dat ? dat.split(","):[114.2909500000, 22.7219600000];
                   	thisA.aa=dat;
                   	
                   	if(thisA.kg){thisA.ajax(1);thisA.kg=false};
                   	if (Math.abs(thisA.weizia[0]-thisA.weizi[0])>0.001||Math.abs(thisA.weizia[1]-thisA.weizi[1])>0.001) {
                   		thisA.ajax(0);
                   		thisA.weizia=thisA.weizi;
                   	};
                
                	
                });
                !(window.Android || window.JsCallOC)&&window.javaCallJs();


			},
			setgo: function() {
				//this.$router.go(0);
				//alert('刷新');
				var thisA = this;

				this.map.clearMap();
				var wzico = new AMap.Icon({
					image: this.imgwz,
					imageSize: new AMap.Size(20, 40),
					size: new AMap.Size(20, 40)
				})
				new AMap.Marker({
					map: this.map,
					position: this.weizi,
					title: 'my',
					offset: new AMap.Pixel(-10, -20),
					icon: wzico
				})
				var lsico = this.lsico;
				//console.log(lsico);
				var infoWindow = new AMap.InfoWindow({
					offset: new AMap.Pixel(60, -20),
					isCustom: true,
					autoMove: true,
					closeWhenClickMap: true,
					showShadow: true
				});
				for(let i = 0; i < lsico.length; i++) {
					var icon = new AMap.Icon({
						image: this.imgls,
						imageSize: new AMap.Size(36, 41.5),
						size: new AMap.Size(36, 41.5)
					});
					var marker = new AMap.Marker({
						map: this.map,
						position: [lsico[i].lng, lsico[i].lat],
						title: lsico[i].typeName,
						//label: lsico[i].typeName,
						topWhenClick: true,
						offset: new AMap.Pixel(-18, -20.75),
						icon: icon
					})
					var lnglat = new AMap.LngLat(this.weizi[0], this.weizi[1]).distance([lsico[i].lng, lsico[i].lat]);

					lnglat = lnglat < 1000 ? Math.floor(lnglat) + 'm' : (lnglat / 1000).toPrecision(3) + 'km';
					console.log(lnglat);
					marker.content = "<div style='background-color:rgb(246,65,65) ;font-size:13px ;color:#fff ;height:35px ; border-radius:60px 60px  60px 0 ;width: 125px;padding:12px 0 ;'><p style='padding: 0 0 0 19.5px;margin: 0 0 5.3px; height:14px'>类型 : " + lsico[i].typeName + "</p><p style='padding: 0 0 0 19.5px;margin: 0 0 5.312px; height:14px'>距离 : " + lnglat + "</p></div>";
					marker.on('click', markerClick);
					marker.emit('click', {
						target: marker
					});
				}


        
				var yzico = this.yzico;
				for(let j = 0; j < yzico.length; j++) {
					var icon = new AMap.Icon({
						image: this.imgyizi,
						imageSize: new AMap.Size(36, 41.5),
						size: new AMap.Size(36, 41.5)
					});
					let marker = new AMap.Marker({
						map: this.map,
						position: [yzico[j].lng, yzico[j].lat],
						title: yzico[j].umbrellaPileNum,
						topWhenClick: true,
						offset: new AMap.Pixel(-18, -20.75),
						icon: icon
					})
					let pos = [yzico[j].lng, yzico[j].lat];
					let data = yzico[j];
					
					marker.id=j;
					marker.on('click', function() {
						
						var lnglat = new AMap.LngLat(thisA.weizi[0], thisA.weizi[1]).distance(pos);
						//lnglat=6575000;
						if(lnglat < 1000) {
							thisA.yzdata.jl = Math.floor(lnglat);
							thisA.yzdata.m = 'm';
						} else {
							thisA.yzdata.jl = (lnglat / 1000).toString().substr(0, 3);
							thisA.yzdata.m = 'km';
						};
						thisA.yzdata.kj = data.availableNum;
						var un=data.umbrellaBorrowList
						thisA.yzdata.ls1 = un[0]?un[0].num:0;
						thisA.yzdata.ls2 = un[1]?un[1].num:0;
						thisA.yzdata.ls3 = un[2]?un[2].num:0;
						thisA.yzdata.ls4 = un[3]?un[3].num:0;
						thisA.yzdata.ls5 = un[4]?un[4].num:0;
                         
						//console.log(thisA.yzdata.jl);
						thisA.marker.id==this.id?thisA.axs = !thisA.axs:thisA.axs=true;
						thisA.marker=this
					});
                   
				}
				var map = this.map;

				function markerClick(e) {
					//console.log(e);
					//console.log(this);
					infoWindow.setContent(e.target.content);
					infoWindow.open(map, e.target.getPosition());
				}
			},
			kefu: function() {
				//alert('客服');
				console.log('客服');
				 xwd.function21008();
			},
			sys: function() {
				//alert('扫一扫');
				console.log('扫一扫');
				//window.open("http://192.168.0.200:8080/vue/dist/index.html#/register")
				var thisA = this;
				xwd.function21002(function (da) {
					
					var patt1=/umjoys{1}/;
					var patt2=/\d{10}/;
					var patt3=/\d{10,15}/;
//					if (da=='pc') {
//							thisA.textal='这是电脑';
//                     		thisA.kg1=true;
//                     		return;
//					}
					if ((patt1.test(da)&&patt2.test(da))||(da.length>=10&&da.length<=15&&patt3.test(da))){
						
						xwd.lenum=patt2.exec(da);
						var data={'userId':1,'umbrellaNum':'using123'};
							$.ajax({
								type:"post",
								url: config.url + "/xwd/umbrella/unlockByUmbrellaNum",
								/*url写异域的请求地址*/
								data:data,
								dataType: "json",
								success: function(dat) {
									if(dat.status==0){
									thisA.$router.push({path:'/using'});
									}else if(dat.status==1){
										thisA.textal='伞的电量过低,暂时无法使用!';
		                       			thisA.kg1=true;
									}else if(dat.status==2){
										thisA.textal='伞已损坏,暂时无法使用!';
		                       			thisA.kg1=true;
									}
								},
		                       error:function  (dat) {
		                       	thisA.textal='开锁失败'
								thisA.kg3=false;
								thisA.kg2=true;
								var time= setTimeout(function  () {
									thisA.kg2=false;
									
								},1000)
		                       //thisA.$router.push({path:'/using'});
		                       }  
							})
						
						
						
					}else{
					    thisA.textal='数据错误';
                       	thisA.kg1=true;
					};
					
					
				});
				//thisA.$router.push({path:'/using'})
				!(window.Android||window.JsCallOC)&&window.javaCallJs('http://server.umjoys.com/index.php/Api/Download/down?d=0000000043&type=1');
			},
			yz: function() {
				//alert('验证');
				console.log('验证');
				xwd.function21009();
			},
			ajax:function  (da) {
				var thisA = this;
				
					$.ajax({
						type:"get",
						url: config.url + "/xwd/umbrella/getPerimeterUmbrella?lng="+thisA.weizi[0]+"&lat="+thisA.weizi[1],
						
						/*url写异域的请求地址*/
						//data:data,
						dataType: "json",
						success: function(dat) {
							
							if(dat.status==0){
							dat.perimeterUmbrellaList?thisA.lsico=dat.perimeterUmbrellaList:'';
							dat.perimeterUmbrellaPileList?thisA.yzico=dat.perimeterUmbrellaPileList:'';
							thisA.setgo();
							if(da)thisA.diwei();
							}else{
								console.log(dat.message);
								thisA.setgo();
								if(da)thisA.diwei();
							}
						},
                       error:function  (dat) {
                       
                       	thisA.setgo();
                        if(da)thisA.diwei();
                       }  
					})
			}
		}
	}
</script>

