		left: 0;
		top: 0;
		width: 100%;
		height: 28.45866666666667rem;
	    display: none;
		transition: all 1s;
		z-index: 99;
	}
	
	.fu-ll {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 28.45866666666667rem;
