		z-index: -10;
	}
	 .fu button{ font-size:0.64rem ;
   	   	width:77.4%;padding: 0.64rem 0  ;margin:0.613rem 0 0 1.45rem ; color: #FFFFFF;background: linear-gradient(to right, #f55b5c ,#d23a58);border-radius: 2rem; 
   	   }
