		background-color: rgb(246, 65, 65);font-size: 0.5973rem;color: #fff;height: 1.536rem;
		border-radius: 1.28rem 1.28rem 1.28rem 0;
		width: 5.312rem;
		padding: 0.512rem 0;
	}
	
