		left: 0.853rem;
		height: 1.194666666666667rem;
	}

	.hade-div {
		text-align:center;
		position: absolute;
		top: 1.576rem;
		left: 2.28rem;
		width: 9.11rem;
		z-index: 10;
		height: 1.706rem;
		font-size: 0.655rem;
		line-height: 1.706rem;
	}
	
	.hade-div {
		padding:0 1.1306rem;
		background-color: rgba(0, 0, 0, 0.7);
		border-radius: 1rem;
	}
	
	.hade-div img {
		z-index: -1;
		top: 0.7rem;
		right: 1.1rem;
		left: auto;
		width: 0.3rem;
	}
	.hade-diva {
		text-align: center;
		position: absolute;
		top: 9.386rem;
		left: 5.226rem;
		width: 5.546rem;
		z-index: 299;
		height: 4.266rem;
		font-size: 0.746rem;
		line-height: 5.706rem;
		color:#FFFFFF ;
	}
	
	.hade-diva {
		
		background-color: rgba(0, 0, 0, 0.7);
		border-radius: 0.1rem;
		
	}
	
	.hade-diva img {
		position: absolute;
		top: 0.938rem;
		right: 2.34rem;
		width: 0.938rem;
	}
	.hade-diva .v{
		width: 1.28rem;
		right: 2.04rem;
	}
	.hade-axs {
		width: 100%;
		position: absolute;
		top:0 ;
		height: 6.1866rem;
		background-color: #FFFFFF;
		color: #000000;
		z-index: 10;
	}
	.hade-axs .qq1 {
		margin-left: 1.813rem;
	}
	.hade-axs .qq {
		position: relative;
		padding: 0.7893rem 1.386rem 0;
		width: 2.9866rem;
		float: left;
	}
	

	
	.hade-axs .qq div {
		background-image: url(./img/yq3x.png);
		background-size: 3.413rem;
