							            "lat": "28.56565",
							            "distance": 199.999,
							            "availableNum": 20
							        },
							        {
							            "umbrellaPileNum": "56766534",
							            "lng": "124.21556",
							            "lat": "28.56565",
							            "distance": 199.999,
							            "availableNum": 30
							        }
							    ]
							},
