					isCustom: true,
					autoMove: true,
					closeWhenClickMap: true,
					showShadow: true
				});
				
				

        
				var yzico = this.yzico;
				
				for (var i=0;i <yzico.length; i++) {
					
				 for (var k=1;k <yzico.length; k++) {
				 	
					if(yzico[i].distance>yzico[k].distance){
						var a = yzico[i];
						yzico[i]=yzico[k];
						yzico[k]=a;
						--i;
					}
				}
				}
				for(let j = 0; j < yzico.length; j++) {
					var icon = new AMap.Icon({
						image: this.imgyizi,
						imageSize: new AMap.Size(36, 41.5),
						size: new AMap.Size(36, 41.5)
					});
					let marker = new AMap.Marker({
						map: this.map,
						position: [yzico[j].lng, yzico[j].lat],
						title: yzico[j].umbrellaPileNum,
						//topWhenClick: true,
						shape:{circle:[0,0,0],type:'circle'},
						offset: new AMap.Pixel(-18, -20.75),
						icon: icon
					})
					var map = this.map;
					
					var lnglat = new AMap.LngLat(this.weizi[0], this.weizi[1]).distance([yzico[j].lng, yzico[j].lat]);
                    
					lnglat = lnglat < 1000 ? Math.floor(lnglat) + 'm' : (lnglat / 1000).toPrecision(3) + 'km';
					marker.content = "<div style='line-height:21px;text-align: center;background-repeat: no-repeat;background-image: url("+thisA.imgls+");background-size:100px; font-size:14px ;color:#f2f2f5 ;height:35px ;width: 100px;'>推荐还伞点</div>";
					if (j==0) {
						infoWindow.setContent(marker.content);
					    infoWindow.open(map,[yzico[j].lng, yzico[j].lat]);
					}
					
					
				}
				

			
			},
			kefu: function() {
				//alert('客服');
				console.log('客服');
				 xwd.function21008();
			},
			sys: function() {
				//alert('扫一扫');
