						url: config.url + "/xwd/umbrella/delayedReturnUmbrella",
						/*url写异域的请求地址*/
						data:data,
						dataType: "json",
						success: function(dat){
							if(dat.status==0){
								thisA.textal='还伞成功'
								thisA.kg3=true;
								thisA.kg2=true;
								var time= setTimeout(function  () {
									thisA.kg2=false;
									thisA.$router.push({path:'/'})
								},1000)
								
							
							}else {
								thisA.textal='还伞失败'
								thisA.kg3=false;
								thisA.kg2=true;
								var time= setTimeout(function  () {
									
									thisA.kg2=false;
									
								},1000)
							}
							
						},
                       error:function  (dat) {
                       
                       thisA.kg1=false;
                       }  
					})
				
			},
			ajax:function  (da) {
				var thisA = this;
				 // var data={'userId':xwd.leid,'umbrellaNum':xwd.lenum};
				  var data={'lng':thisA.weizi[0],'lat':thisA.weizi[1],'userId':1,'umbrellaNum':'using123'};
					$.ajax({
						type:"post",
						url: config.url + "/xwd/umbrella/getUmbrellaHomePage",
						/*url写异域的请求地址*/
						data:data,
						dataType: "json",
						success: function(dat){
							if(dat.status==0){
							thisA.lsico=dat;
							thisA.yzico=dat.perimeterUmbrellaPileList;
							
							thisA.setgo();
							if(da)thisA.diwei();
							}else{
								console.log(dat.message);
								thisA.setgo();
								if(da)thisA.diwei();
							}
							
						},
                       error:function  (dat) {
                       
                       	thisA.setgo();
                        if(da)thisA.diwei()
                       }  
					})
			}
		}
	}
</script>

